<template>
<div>
        <v-card elevation="8" outlined tile class="mx-auto my-12"
    max-width="674">
    <v-card-title>Bull's Eye</v-card-title>
    <v-row>
        <v-col cols="12">
            <v-card-text>Predict: {{eventStatement}}</v-card-text>
        </v-col>
    </v-row>
    <v-row>
        <v-col cols="6">
            <v-card-text>End: {{endTime}}</v-card-text>
        </v-col>
    </v-row>
    <v-row>
        <v-col cols="6">
            <v-text-field v-model="predictionValue" label="Prediction"></v-text-field>
        </v-col>
        <v-col cols="6">
            <v-btn color="primary" @click="makePrediction">Submit</v-btn>
        </v-col>
    </v-row>
    <v-row>
        <v-col cols="12">
             <v-card-text>Your Balance: {{userBalance}}</v-card-text>
        </v-col>
        
    </v-row>
    </v-card>
</div> 
</template>
<script>
import BullsEye from "../lib/contracts/bullsEye";
import {mapGetters, mapMutations} from 'vuex';

export default {
    name: 'BullsEye',
    mounted: async function(){
        this.eventID = this.$route.params.eventID;
        await this.init();
    },
    computed: {
    ...mapGetters([
        "network",
        "getSelectedInstrument",
        "account",
        "loading",
        "balance"
    ]),
    
    },
    data: function(){
        return {
            eventID: '',
            eventStatement: '',
            endTime: '',
            predictionValue: '',
            userBalance: ''
        }
    },
    methods: {
        init: async function(){
            this.client = new BullsEye({
                network: this.network,
                instrument: this.getSelectedInstrument,
            });
            await this.client.init();
            const event = await this.client.signerContract.bullsEyeEvents(this.eventID);
            console.log("event", event);
            this.endTime = new Date(event.endTime*1000).toISOString().substr(0, 19);
            this.eventStatement = event.statement;
            const account = await this.client.metamaskProvider.listAccounts(0);
            console.log(account)
            this.userBalance = await this.client.signerContract.balances(account[0]);
            console.log(this.userBalance)
        },
        makePrediction: async function(){
            const withSigner = this.client.signerContract.connect(this.metamaskProvider.getSigner());
            const bidAmount = 0;
            await withSigner.makePrediction(this.eventID, bidAmount, this.predictionValue*1, {gasLimit: 500000}).catch(e => alert('Error'));
        }
    }
}
</script>

    