const {abi} = require('@predmark/dapps.contracts/src/contracts/abi/BullsEye.json'),
    clientContract = require('./clientContracts');

class BullsEye extends clientContract {
    constructor(options) {
        super({
            abi,
            contractAddress: options.network.bullsEye,
            network: options.network,
            BICONOMY_API_KEY: options.network.biconomy_api_key,
            instrument: options.instrument
        });
    }
    async makePrediction(startTime, endTime, statement, bandsAndPayout, creatorCommissionBasisPoints, precision, oracle, isRealMoney, amount, userPrediction, onDemandCreate){
        const withSigner = this.signerContract.connect(this.metamaskProvider.getSigner())
        return await withSigner.makePrediction(startTime, endTime, statement, bandsAndPayout, creatorCommissionBasisPoints, precision, oracle, isRealMoney, amount, userPrediction, onDemandCreate,{gasLimit: 1000000});
    }
    async getEventID(startInSecond, endInSecond, name, oracle){
        const eventID = await this.signerContract.getEventID(startInSecond, endInSecond, name, oracle);
        return eventID;    
    }

    async getEventDetails(startInSecond, endInSecond, name, oracle){
        let eventId = await this.getEventID(startInSecond, endInSecond, name, oracle)
        let eventData =  await this.signerContract.bullsEyeEvents(eventId);
        return {
            id: eventId,
            data: eventData
        }
    }

    async getPlayerPrediction(eventID, playerId, index) {
        return await this.signerContract.playerPredictions(eventID, playerId, index)
    }
}

module.exports = BullsEye;